.help {
    padding: 110px 0 80px 0;
    &__icon {
        display: block;
        max-height: 120px;
        max-width: 120px;
        margin-left: 150px;
    }
    &__wrapper {
        margin-top: 16px;
        margin-left: 150px;
        display: flex;
    }
    &__title {
        margin: 16px 74px 0 0;
        width: 492px;
        color: var(--brand-color);
        font-size: 48px;
        font-weight: 800;
        line-height: 56px;
        letter-spacing: -0.48px;
    }
    &__info {
        width: 468px;
        color: #4A556C;
        font-size: 17px;
        font-weight: 400;
        line-height: 25px; 
    }
    &__areas {
        margin: 16px 0 0 150px;;
        display: flex;
        &-item {
            padding: 40px;
            width: 380px;
        }
        &-icon {
            display: block;
            max-width: 72px;
            min-height: 72px;
        }
        &-title {
            margin-top: 32px;
            color: #041434;
            font-size: 24px;
            font-weight: 700;
            line-height: 32px;
            letter-spacing: -0.72px;
        }
        &-text {
            margin-top: 16px;
            color: #4A556C;
            font-size: 16px;
            font-weight: 400;
            line-height: 24px;
            letter-spacing: -0.32px;
        }
    }
}