.header {
    position: relative;
    height: 60px;
}
.menu {
    position: fixed;
    z-index: 999;
    height: 60px;
    width: 1440px;
    padding: 0 75px 0 85px;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &__logo-link {
        width: 150px;
    }
    &__logo-img {
        width: 100%;
        height: 100%;
    }
    &__items {
        height: 40px;
        width: 576px;
        display: flex;
        justify-content: space-between;
    }
    &__list {
        width: 356px;
        height: 100%;
        list-style-type: none;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__list-link {
        color: #4A556C;
        text-align: right;
        font-size: 15px;
        font-weight: 400;
        letter-spacing: -0.3px;
        text-decoration: none;
        transition: 0.6s all;
        position: relative;
        &::before {
            content: '';
            width: 0;
            height: 1px;
            background-color: var(--comlementary-color);
            position: absolute;
            top: calc(100% + 1px);
            transition: 0.6s all;
        }
        &:hover {
            color: var(--comlementary-color);
            &::before {
                width: 100%;
            }
        }
    }
}