.promo {
    min-height: 677px;
    background-color: var(--cream-color);
    padding-top: 74px;
    position: relative;
    &__icon {
        display: block;
        position: absolute;
        top: -28px;
        left: 34px;
        max-height: 120px;
        max-width: 120px;
    }
    &__wrapper {
        padding: 0 166px 0 85px;
        display: flex;
        justify-content: space-between;
    }
    &__info {
        margin-top: 40px;
        width: 545px;
    }
    &__descr {
        color: #4A556C;
        font-size: 16px;
        font-weight: 400;
        line-height: 24px;
        letter-spacing: -0.32px;
        opacity: 0.4;
    }
    &__header {
        margin-top: 6px;
        color: var(--brand-color);
        font-size: 72px;
        font-weight: 800;
        line-height: 120%;
        letter-spacing: -0.72px;
    }
    &__text {
        margin-top: 18px;
        color: #4A556C;
        font-size: 18px;
        font-weight: 400;
        line-height: 160%;
    }
    &__btn {
        margin-top: 18px;
    }
    &__illustr {
        position: relative;
        width: 462px;
        height: 603px;
    }
    &__img {
        position: relative;
        top: 26px;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    &__picture-descr {
        width: 237px;
        height: 104px;
        background-color: #fff;
        position: absolute;
        top: -21px;
        left: -116px;
        padding: 24px;
        display: grid;
        grid-template: 32px 24px / 56px 117px;
        column-gap: 16px;
    }
    &__picture-cross {
        grid-row: 1 / 3;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    &__picture-title {
        color: var(--brand-color);
        font-size: 24px;
        font-weight: 800;
        line-height: 32px;
        letter-spacing: -0.72px;
    }
    &__picture-sign {
        color: #4A556C;
        font-size: 12px;
        line-height: 24px;
        font-weight: 400;
        letter-spacing: -0.12px;
    }
}